// ** React Imports
import {Link, useNavigate} from 'react-router-dom'
import {useContext, useEffect, useState} from 'react'
import {Can} from '@src/utility/context/Can.js'
// ** Custom Components
import Avatar from '@components/avatar'

// ** Utils
import axios from '@src/configs/axios/axiosConfig'
//import { isUserLoggedIn } from '@utils'
import {activeOrgDetails} from '@src/views/apps/organization/store/index.js'
// ** Store & Actions
import {useDispatch, useSelector} from 'react-redux'
import {handleLogout} from '@store/authentication'
import {AbilityContext} from '@src/utility/context/Can'

// ** Third Party Components
import {Power, Settings, User} from 'react-feather'

// ** Reactstrap Imports
import {DropdownItem, DropdownMenu, DropdownToggle, UncontrolledDropdown} from 'reactstrap'

// ** Default Avatar Image
import defaultAvatar from '@src/assets/images/avatars/avatar-blank.png'
import {activeOrganization, activeOrganizationid, loggedInUser, orgUserId} from '@src/helper/sassHelper'

const userId = orgUserId()
const loginuser = loggedInUser()
const activeOrg = activeOrganization()
const activeOrgId = activeOrganizationid()

const UserDropdown = () => {
    // ** Store Vars
    const dispatch = useDispatch()

    // ** State
    const [userData, setUserData] = useState(null)
    const [organizations, setOrganizations] = useState([])
    const [driveId, setDrvieId] = useState('')

    const ability = useContext(AbilityContext)
    const navigate = useNavigate()
    const store = useSelector(state => state.auth)
    //** ComponentDidMount

    const getOrganizationUserImage = async (id) => {
        const user = {id}
        const res = await axios.post('organizationusers/get', user)
        const data = res.data.organizationuser
        if (res.data.organizationuser.fileid) localStorage.setItem('userImage', res.data.organizationuser.fileid)
        const abilitytemp = []
        data.ability.forEach(obj => {
            const action = obj.substring(obj.lastIndexOf("-") + 1)
            const resource = obj.replace(obj.substring(obj.lastIndexOf("-")), '')
            const opt = {action, subject: resource}
            abilitytemp.push(opt)
        })
        localStorage.setItem('ability', JSON.stringify(abilitytemp))
        await ability.update(abilitytemp)
        setDrvieId(res.data.organizationuser.fileid || '')
    }

    const getOrganization = async (id) => {

        const user = {userId: id}
        const res = await axios.post('organizationusers/usersorganization', user)
        if (res.data.organizations.length === 0) {
            navigate('/organization/create')
        }
        setOrganizations(res.data.organizations)
        const activeOrg = res.data.organizations.find((obj) => obj.isdefault === true)
        const org = activeOrg || res.data.organizations[0]
        localStorage.setItem('activeOrganization', JSON.stringify(org))
        await dispatch(activeOrgDetails(org))
        await getOrganizationUserImage(org.organizationuserid)
    }

    useEffect(async () => {
        setUserData(JSON.parse(localStorage.getItem('userData')))
        const temp = Object.keys(store.userData).length === 0 ? loginuser : (typeof store.userData === 'string' ? JSON.parse(store.userData) : store.userData)
        if (temp.id) {
            getOrganization(temp.id)
        }
    }, [store.userData])

    const setActiveOrg = async (org) => {
        localStorage.setItem('activeOrganization', JSON.stringify(org))
        await dispatch(activeOrgDetails(org))
        await getOrganizationUserImage(org.organizationuserid)
        navigate('/dashboard')
        navigate(0)
    }

    //** Vars
    const img = localStorage.getItem('userImage')
    const userAvatar = img ? img : (userData && userData.avatar) || defaultAvatar

    return (
        <UncontrolledDropdown tag='li' className='dropdown-user nav-item'>
            <DropdownToggle href='/' tag='a' className='nav-link dropdown-user-link' onClick={e => e.preventDefault()}>
                <div className='user-nav d-sm-flex d-none'>
                    <span className='user-name fw-bold'>{(userData && userData['name']) || 'John Doe'}</span>
                    <span className='user-status'>{(activeOrg && activeOrg.rolename) || 'Admin'}</span>
                </div>
                {driveId === '' &&
                    <Avatar img={userAvatar} imgHeight='40' imgWidth='40' status='online'/>
                }{driveId !== '' &&
                <img className='rounded' height={40} width={40} src={`https://drive.google.com/uc?id=${driveId}`}
                     alt="Profile Picture"/>
            }
            </DropdownToggle>
            <DropdownMenu end style={{minWidth: '26rem'}}>
                <DropdownItem tag={Link} to={`/profile/view/${userId}`}>
                    <User size={14} className='me-75'/>
                    <span className='align-middle'>Profile</span>
                </DropdownItem>
                {/* <DropdownItem tag={Link} to='/apps/email'>
          <Mail size={14} className='me-75' />
          <span className='align-middle'>Inbox</span>
        </DropdownItem>
        <DropdownItem tag={Link} to='/apps/calendar'>
          <Calendar size={14} className='me-75' />
          <span className='align-middle'>Calendar</span>
        </DropdownItem>
        <DropdownItem tag={Link} to='/apps/chat'>
          <MessageSquare size={14} className='me-75' />
          <span className='align-middle'>Chats</span>
        </DropdownItem> */}
                <DropdownItem divider/>
                <h5 className='px-1'>Organizations</h5>
                {
                    organizations.map((obj, k) => {
                        return (<p key={k}
                                   className={`cursor-pointer px-1 pb-0 pt-0 ${obj.id === activeOrgId ? 'text-success' : ''}`}
                                   onClick={() => setActiveOrg(obj)}> {obj.name} </p>)
                    })
                }
                <DropdownItem divider/>
                <Can I={'read'} a={'setting'}>
                    <DropdownItem tag={Link} to='/account-settings'>
                        <Settings size={14} className='me-75'/>
                        <span className='align-middle'>Settings</span>
                    </DropdownItem>
                </Can>
                {/* <DropdownItem tag={Link} to='/pages/faq'>
          <HelpCircle size={14} className='me-75' />
          <span className='align-middle'>FAQ</span>
        </DropdownItem> */}
                <DropdownItem tag={Link} to='/login' onClick={() => dispatch(handleLogout())}>
                    <Power size={14} className='me-75'/>
                    <span className='align-middle'>Logout</span>
                </DropdownItem>
            </DropdownMenu>
        </UncontrolledDropdown>
    )
}

export default UserDropdown
