// ** React Imports
import { Link, useNavigate } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Third Party Components
import { CheckCircle, FilePlus, PlusCircle, ShoppingCart, User, UserPlus } from 'react-feather'

// ** Reactstrap Imports
import { UncontrolledPopover, PopoverBody, Row, Col, NavItem } from 'reactstrap'
import { useState } from 'react'

const GlobalAddDropDown = () => {

  const [popoverOpen, setPopoverOpen] = useState(false)
  const navigate = useNavigate()

  const gotoPage = (page) => {
    navigate(page)
    setPopoverOpen(!popoverOpen)
  }

  return (
    <Row>
      <NavItem>
        <PlusCircle id='popRight' className='ficon cursor-pointer' />
      </NavItem>
      <UncontrolledPopover placement='bottom' target='popRight' isOpen={popoverOpen} toggle={() => setPopoverOpen(!popoverOpen)}>
        <PopoverBody >
          <Row className='p-1'>
            <h5 >Task Activity</h5>
            <Can I='write' a='task'>
              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/task/add`)} >
                <CheckCircle />Add Task
              </Col>
            </Can>
            <Can I='write' a='invoice'>

              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/invoice/add`)} >
                <FilePlus />Add Invoice
              </Col>
            </Can>
            <Can I='write' a='invoice'>

              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/proposal/add`)} >
                <FilePlus />Add Proposal
              </Col>
            </Can>
          </Row>
          <Row className='p-1' >
            <h5 >Setting Up</h5>
            <Can I='write' a='client'>
              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/clients/add`)} >
                <User />Add Client
              </Col>
            </Can>
            <Can I='write' a='service'>

              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/services/add`)} >
                <ShoppingCart />Add Service
              </Col>
            </Can>
            <Can I='write' a='team'>

              <Col md={4} className='pt-1 cursor-pointer' onClick={() => gotoPage(`/team/add`)} >
                <UserPlus /> Add Team
              </Col>
            </Can>
          </Row>
        </PopoverBody>
      </UncontrolledPopover>
    </Row>
  )
}

export default GlobalAddDropDown
